/* zoomed entry */
@keyframes zoomIn {
	from {
		opacity: 0;
		transform: scale3d(0.3, 0.3, 0.3);
	}

	50% {
		opacity: 1;
	}
}

/* fades in to screen */
@keyframes fadeIn {
	from {
		opacity: 0;
	}

	100% {
		opacity: 1;
	}
}

/* spin */
@keyframes spin {
	from {
		transform: rotateY(0deg);
	}
	to {
		transform: rotateY(360deg);
	}
}
